import AppModel from '../models/AppModel';
import AppView from '../views/AppView';
import MediaView from '../views/AppView/MediaView';

export default class App {
  constructor() {
    this.state = {
      url: 'https://www.googleapis.com/youtube/v3/search?',
      part: 'snippet',
      key: 'AIzaSyBty70m7qGoetuK-VgoMRdD8MRbNPsvP8M',
      type: 'video',
      maxResults: 15,
      pageToken: '',
    };
  }

  async start() {
    const view = new AppView();
    // init: first screen
    view.renderInit();
    const model = new AppModel(this.state);
    document.querySelector('input').addEventListener('keydown', (event) => {
      if (event.keyCode === 13) {
        model.getSearchIds().then((result) => {
          // render first 15 results
          view.renderResults(result);
          // add event listeners
          App.eventListener(this.state);
        });
      }
    });
  }

  static eventListener(state) {
    // load next 15 items
    function loadMore() {
      const C = document.querySelector('.main').style.getPropertyValue('--n');
      document.querySelector('.main').style.setProperty('--n', +C + 15);
      const view = new AppView();
      const model = new AppModel(state);
      model.getSearchIds().then((result) => {
        view.renderResults(result, 4);
      });
    }
    // event
    function scrollEvent(e, key) {
      // right border offset
      let offRight;
      if (key === 10) {
        // mobile mode
        offRight = e.screenX - e.clientX;
      } else {
        offRight = document.querySelector('.main').getBoundingClientRect().right;
      }
      if (!e.deltaY && key !== 10) {
        // !e.deltaY when touchevent; key = direction
        e.deltaY = key * document.documentElement.clientWidth;
      }
      if (e.deltaY) {
        if (e.deltaY !== 0) {
          // mouse scroll: y => x
          window.scroll(window.scrollX + e.deltaY, window.scrollY);
          e.preventDefault();
        }
      }
      // current offset
      App.offset = document.documentElement.clientWidth;
      // page counter
      const pagesCount = document.querySelector('.main').offsetWidth / document.documentElement.clientWidth;
      const currentPage = offRight / document.documentElement.clientWidth;
      document.getElementsByClassName('button')[1].innerHTML = `<p> ${Math.floor(pagesCount - currentPage + 1)}</p>`;
      // load more items when user window close to the edge of div block
      if (offRight < App.offset * 1.5) {
        loadMore();
      }
    }
    // for in-use resize lovers
    function resize(x) {
      const flag = x.currentTarget.media.slice(11, 15);
      const mediaview = new MediaView(x, flag);
      mediaview.resize();
    }

    const main = document.querySelector('.main');
    const prevButton = document.querySelector('.button');
    const nextButton = document.getElementsByClassName('button')[2];
    main.addEventListener('wheel', scrollEvent);
    if (/Android|webOS|iPhone|iPad|iPod/i.test(navigator.userAgent)) {
      main.addEventListener('click', (e) => {
        scrollEvent(e, 10);
      }, false);
    } else {
      main.addEventListener('touchstart', scrollEvent, false);
    }
    nextButton.addEventListener('mousedown', (e) => {
      scrollEvent(e, 1);
    }, false);
    prevButton.addEventListener('mousedown', (e) => {
      scrollEvent(e, -1);
    }, false);
    const smalScreen = window.matchMedia('(max-width: 500px)');
    const bigScreen = window.matchMedia('(max-width: 800px)');
    smalScreen.addListener(resize);
    bigScreen.addListener(resize);
  }
}
