export default class AppModel {
  constructor(state) {
    this.state = state;
  }

  static async searchFullInfo(ids, state) {
    // second request: get full item statistics
    const startUrl = 'https://www.googleapis.com/youtube/v3/videos?';
    const url = `${startUrl}&key=${state.key}&id=${ids}&part=snippet,statistics`;
    const response = await fetch(url);
    const data = await response.json();
    return data;
  }

  static extractClipIds(data, info) {
    // ids from first request
    let ids = '';
    ids += data.items.map(clip => `${clip.id.videoId}`);
    return AppModel.searchFullInfo(ids, info);
  }

  async getSearchIds() {
    // first request: search
    this.searchValue = document.querySelector('input').value;
    const url = `${this.state.url}&key=${this.state.key}&type=${this.state.type}&part=${this.state.part}&pageToken=${this.state.pageToken}&maxResults=${this.state.maxResults}&q=${this.searchValue}`;
    const response = await fetch(url);
    const data = await response.json();
    this.state.pageToken = data.nextPageToken;
    return AppModel.extractClipIds(data, this.state);
  }
}
