export default class AppView {
  constructor() {
    // create element, set attribute, fill text node
    this.createEl = (elemName, className, parentNode, key) => {
      const el = document.createElement(`${elemName}`);
      el.className = className;
      document.querySelectorAll(`${parentNode}`)[key].appendChild(el);
    };
    this.setAttr = (nodeClassName, attr, value, i) => {
      const node = document.getElementsByClassName(nodeClassName)[i];
      node.setAttribute(`${attr}`, `${value}`);
    };
    this.fillText = (parentNode, i, text, br) => {
      document.querySelectorAll(`${parentNode}`)[i].appendChild(document.createTextNode(`${text}`));
      if (br) {
        document.querySelectorAll(`${parentNode}`)[i].innerHTML += '<br>';
      }
    };
  }

  renderInit() {
    // search area + onkeydown: press enter to start search + blank main window
    window.scrollTo(0, 0);
    this.createEl('div', 'search-area', 'body', 0);
    this.createEl('input', 'search', '.search-area', 0);
    this.input = document.querySelector('input');
    this.input.setAttribute('type', 'search');
    this.input.setAttribute('placeholder', 'srch');
    this.createEl('div', 'main', 'body', '0');
    this.main = document.querySelector('.main');
    this.main.style.setProperty('--n', 15);
    const { clientWidth } = document.documentElement;
    if (clientWidth < 800 && clientWidth > 500) {
      document.querySelector('.main').style.setProperty('width', 'calc((var(--n)*100%)/2)');
    } else if (clientWidth < 500) {
      document.querySelector('.main').style.setProperty('width', 'calc((var(--n)*100%))');
    } else {
      document.querySelector('.main').style.setProperty('width', 'calc((var(--n)*100%)/4)');
    }
  }

  renderResults(data, key) {
    // main section
    const main = document.querySelector('.main');
    if (main.innerHTML !== '' && key !== 4) {
      main.innerHTML = '';
      main.style.setProperty('--n', 15);
      window.scrollTo(0, 0);
    }
    if (data.items.length >= 1) {
      const childCount = document.querySelector('.main').childElementCount;
      for (let i = childCount; i < childCount + 15; i += 1) {
        const Z = i - childCount;
        // block
        this.createEl('div', 'block', '.main', 0);

        // pic
        this.createEl('img', 'image-thumb', '.block', i);
        this.setAttr('image-thumb', 'src', `${data.items[Z].snippet.thumbnails.medium.url}`, i);

        // header + href
        this.createEl('h3', 'descr', '.block', i);
        this.createEl('a', 'arch', '.descr', i);
        this.setAttr('arch', 'href', `https://www.youtube.com/watch?v=${data.items[Z].id}`, i);
        this.setAttr('arch', 'target', '_blank', i);
        this.fillText('.arch', i, `${data.items[Z].snippet.title.slice(0, 40)}...`);

        // description
        this.createEl('p', 'text', '.block', i);
        this.fillText('.text', i, `${data.items[Z].snippet.description.slice(0, 80)}...`, 1);
        this.fillText('.text', i, `📅 ${data.items[Z].snippet.publishedAt.slice(0, 10)}`, 1);
        this.fillText('.text', i, `${data.items[Z].snippet.channelTitle}`, 1);
        this.fillText('.text', i, `👁️ ${data.items[Z].statistics.viewCount}`, 1);
      }
    } else {
      // no results
      const div = document.createElement('div');
      const ttle = document.createElement('h3');
      div.className = 'block';
      document.querySelector('.main').appendChild(div);
      ttle.appendChild(document.createTextNode('No Results :('));
      div.appendChild(ttle);
      document.querySelector('.main').style.setProperty('--n', 1);
    }
    if (document.querySelector('.nav-buttons') === null) {
      // navigation
      const srchWidth = document.querySelector('.search').clientWidth;
      this.createEl('div', 'nav-buttons', 'body', 0);
      this.setAttr('nav-buttons', 'style', `width:${srchWidth}px`, 0);

      for (let i = 0; i < 3; i += 1) {
        this.createEl('div', 'button', '.nav-buttons', 0);
      }
    }
  }
}
