export default class MediaView {
  constructor(x, flag) {
    this.x = x;
    this.flag = +flag;
  }

  async resize() {
    // list of on-screen items: 4 -> 2 -> 1
    const main = document.querySelector('.main');
    if (this.flag === 500 && this.x.matches) {
      main.style.setProperty('width', 'calc((var(--n)*100%))');
    } else if (this.flag === 500 && !this.x.matches) {
      main.style.setProperty('width', 'calc((var(--n)*100%)/2)');
    } else if (this.flag === 800 && this.x.matches) {
      main.style.setProperty('width', 'calc((var(--n)*100%)/2)');
    } else if (this.flag === 800 && !this.x.matches) {
      main.style.setProperty('width', 'calc((var(--n)*100%)/4)');
    }
  }
}
